"use client";

import { Button } from "@/components/ui/button";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table";

const timeEntries = [
  {
    id: 1,
    project: "Website Redesign",
    task: "Design homepage",
    member: "Mike Johnson",
    hours: 4.5,
    date: "2024-07-19",
    status: "Approved"
  },
  {
    id: 2,
    project: "Mobile App",
    task: "Fix navigation bug",
    member: "Sarah Wilson",
    hours: 2.0,
    date: "2024-07-19",
    status: "Pending"
  },
  {
    id: 3,
    project: "Backend Services",
    task: "API development",
    member: "Alex Brown",
    hours: 6.0,
    date: "2024-07-18",
    status: "Approved"
  },
  {
    id: 4,
    project: "Marketing Campaign",
    task: "Content creation",
    member: "Lisa Anderson",
    hours: 3.5,
    date: "2024-07-18",
    status: "Approved"
  }
];

export default function TimePage() {
  const getStatusBadge = (status: string) => {
    const colors = {
      Approved: "bg-green-100 text-green-800",
      Pending: "bg-yellow-100 text-yellow-800",
      Rejected: "bg-red-100 text-red-800"
    };
    return (
      <Badge className={colors[status as keyof typeof colors] || "bg-gray-100 text-gray-800"}>
        {status}
      </Badge>
    );
  };

  const totalHours = timeEntries.reduce((sum, entry) => sum + entry.hours, 0);
  const approvedHours = timeEntries.filter(e => e.status === "Approved").reduce((sum, entry) => sum + entry.hours, 0);

  return (
    <div className="flex-1 overflow-auto bg-gray-50">
      <div className="p-6">
        <div className="flex items-center justify-between mb-6">
          <div>
            <h1 className="text-3xl font-bold text-gray-900">Time Tracking</h1>
            <p className="text-gray-600 mt-1">Track and manage team working hours</p>
          </div>
          <Button className="bg-purple-600 hover:bg-purple-700">
            + Log Time
          </Button>
        </div>

        {/* Stats */}
        <div className="grid grid-cols-1 md:grid-cols-4 gap-6 mb-6">
          <Card>
            <CardHeader className="pb-3">
              <CardTitle className="text-sm font-medium text-gray-600">Total Hours</CardTitle>
              <CardDescription className="text-2xl font-bold text-gray-900">{totalHours}h</CardDescription>
            </CardHeader>
          </Card>
          <Card>
            <CardHeader className="pb-3">
              <CardTitle className="text-sm font-medium text-gray-600">Approved Hours</CardTitle>
              <CardDescription className="text-2xl font-bold text-gray-900">{approvedHours}h</CardDescription>
            </CardHeader>
          </Card>
          <Card>
            <CardHeader className="pb-3">
              <CardTitle className="text-sm font-medium text-gray-600">Pending Hours</CardTitle>
              <CardDescription className="text-2xl font-bold text-gray-900">
                {(totalHours - approvedHours).toFixed(1)}h
              </CardDescription>
            </CardHeader>
          </Card>
          <Card>
            <CardHeader className="pb-3">
              <CardTitle className="text-sm font-medium text-gray-600">Team Members</CardTitle>
              <CardDescription className="text-2xl font-bold text-gray-900">
                {new Set(timeEntries.map(e => e.member)).size}
              </CardDescription>
            </CardHeader>
          </Card>
        </div>

        {/* Time Entries Table */}
        <Card>
          <CardHeader>
            <CardTitle>Time Entries</CardTitle>
            <CardDescription>All logged time entries for the team</CardDescription>
          </CardHeader>
          <CardContent>
            <Table>
              <TableHeader>
                <TableRow>
                  <TableHead>Project</TableHead>
                  <TableHead>Task</TableHead>
                  <TableHead>Member</TableHead>
                  <TableHead>Hours</TableHead>
                  <TableHead>Date</TableHead>
                  <TableHead>Status</TableHead>
                  <TableHead>Actions</TableHead>
                </TableRow>
              </TableHeader>
              <TableBody>
                {timeEntries.map((entry) => (
                  <TableRow key={entry.id}>
                    <TableCell className="font-medium">{entry.project}</TableCell>
                    <TableCell>{entry.task}</TableCell>
                    <TableCell>{entry.member}</TableCell>
                    <TableCell>{entry.hours}h</TableCell>
                    <TableCell>{entry.date}</TableCell>
                    <TableCell>{getStatusBadge(entry.status)}</TableCell>
                    <TableCell>
                      <Button size="sm" variant="outline">Edit</Button>
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </CardContent>
        </Card>
      </div>
    </div>
  );
}
